.error {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 90%;
  height: calc(100vh - 180px);
  text-align: center;

  &__text {
    font-size: 1.8rem;
    color: #e84545;

    @media (min-width: 1024px) {
      font-size: 2.5rem;
    }
  }
}
